import React, { Component } from 'react'
import PropTypes from 'prop-types'
import './login.scss'
import { LoginPhNo, LoginContactDetails, LoginOTP } from './LoginSubComponent'
import { BrandSpecificConfig } from '../../../utils/constants'
import { regexCheck } from '../../../utils/regex'
import { browserHistory } from 'react-router'
import _ from 'lodash'
import images from '../../../images/config/images.json'
import { lookup } from '../../../utils/ipinfo'

const MainLogo = '../../../' + _.get(images, 'common.LOGO')
const activeOTPImg = '../../../' + _.get(images, 'common.ACTIVE_OTP')
const defOTPImg = '../../../' + _.get(images, 'common.OTP_IMAGE')

class Login extends Component {
  constructor (props) {
    super(props)
    this.state = {
      loginPageId : 'login',
      otpValue: '',
      recaptcha: '',
      showReCaptcha: false,
      recaptchaCheck: false,
      resendOTP: false,
      ResendOTPRequest: false,
      allowResend: false,
      Phno: '',
      UsrEmailid: '',
      UsrName: '',
      showResendOTPbtn: false,
      frequentCalls: 60,
      showOTPTimer: false,
      showCallOptnBtn: false,
      otpViaCallTimer: 60,
      showCallTimer: false,
      sendOTPCounter: 0,
      selectedCountryObj: _.get(props, 'user.selectedCountryData', {}),
      BtnLoaderStatus: false,
      setFocusOTP: false,
      otpImage: defOTPImg,
      termsAndCondCheck: false,
      configFetched: false,
      phnoLengthAr: _.get(props, 'user.selectedCountryData.AllowedMobileLengths')
        ? props.user.selectedCountryData.AllowedMobileLengths.split(',').map(int => parseInt(int))
        : [10]
    }
  }

  componentWillMount () {
    // let consumerDetails = _.get(this, 'props.user.consumerDetails')
    // if (consumerDetails && consumerDetails.data.isNew) {
    //   this.setState({
    //     loginPageId: 'login'
    //   })
    // } // need to see use cases
    this.props.setglobalLoaderStatus(true)
    // Fetch region details
    lookup((regionCode) => {
      window.CountryCode = regionCode
      window.CountryID = undefined
      let countryPromise = this.props.loadAppConfig()
      countryPromise
      .then(() => {
        this.setState({
          configFetched: true
        })
        let selectedObj = this.props.user.countries
          .filter(country => (country.CountryCode === regionCode))[0]
        this.setState({
          selectedCountryObj: selectedObj ? {
            ...selectedObj,
            label: '+' + selectedObj.PhoneCode,
            value: selectedObj.PhoneCode
          } : {
            ...this.props.user.countries[0],
            label: '+' + this.props.user.countries[0].PhoneCode,
            value: this.props.user.countries[0].PhoneCode
          }
        }, () => {
          this.props.setSelectedCountryData(this.state.selectedCountryObj)
        })
        this.props.setglobalLoaderStatus(false)
      })
    })
  }

  componentDidMount () {
    window.onpopstate = () => {
      window.location.reload()
    }
  }

  componentWillUnmount () {
    // clear timer related intervals
    clearInterval(window.frequentCallsFunc)
    clearInterval(window.otpViaCallFunc)
  }

  componentWillReceiveProps (nextProps) {
    if (this.props !== nextProps) {
      let phnoLengthAr = _.get(nextProps, 'user.selectedCountryData.AllowedMobileLengths')
        ? nextProps.user.selectedCountryData.AllowedMobileLengths.split(',').map(int => parseInt(int))
        : [10]
      this.setState({
        phnoLengthAr
      })
    }
  }

  handleCountryChange = (value) => {
    if (value.CountryID !== this.props.user.selectedCountryData.CountryID) {
      // call appConfig only when selected country has changed
      this.setState({
        selectedCountryObj: value
      })
      this.props.setSelectedCountryData(value)
      this.props.setglobalLoaderStatus(true)
      this.props.loadAppConfig()
      .then(() => {
        this.props.setglobalLoaderStatus(false)
      })
    }
  }

  fetchOTPfunc = (requestObj, viaResendOptn = true) => {
    let reqObj = requestObj
    console.log(this.state.ResendOTPRequest)
    if (this.state.ResendOTPRequest) {
      console.log('if', reqObj)
      reqObj = {
        ...reqObj,
        'ResendOTPRequest': true
      }
    } else {
      console.log('else', reqObj)

      reqObj = {
        ...reqObj
      }
    }
    this.props.generateOTPAsync(reqObj).then((data) => {
      const { data: { sendCaptcha: showReCaptcha, allowResend = true } } = data
      if (allowResend) {
        this.setState({
          allowResend,
        })
      }
      if (viaResendOptn) { // resend option set state execution
        if (requestObj.voiceOtp) { // for resend via call
          this.setState({
            showCallTimer: true
          }, () => {
            window.otpViaCallFunc = setInterval(() => {
              if (this.state.otpViaCallTimer === 0) {
                clearInterval(window.otpViaCallFunc)
                this.setState({
                  otpViaCallTimer: 60,
                  showCallOptnBtn: true,
                  disableResendOtp: false
                })
              } else {
                this.setState({
                  showCallOptnBtn: false,
                  otpViaCallTimer: this.state.otpViaCallTimer - 1
                })
              }
            }, 1000)
          })
        } else { // for resend via msg
          this.setState({
            showOTPTimer: true
          }, () => {
            window.frequentCallsFunc = setInterval(() => {
              if (this.state.frequentCalls === 1) {
                clearInterval(window.frequentCallsFunc)
                this.setState({
                  frequentCalls: 60,
                  showResendOTPbtn: true,
                  disableResendOtp: false
                })
              } else {
                this.setState({
                  showResendOTPbtn: false,
                  frequentCalls: this.state.frequentCalls - 1
                })
              }
            }, 1000)
          })
        }
      } else {
        this.setState({
          showReCaptcha,
          BtnLoaderStatus: false,
          otpFetched: true,
          sendOTPCounter: 8,
          loginPageId : 'otp_page',
          setFocusOTP : true
        }, () => {
          window.sendOTPCounterFunc = setInterval(() => {
            // timer functions to display counters for enabling re-send options
            if (this.state.sendOTPCounter === 1) {
              clearInterval(window.sendOTPCounterFunc)
              this.setState({
                sendOTPCounter: 0,
                showResendOTPbtn: true,
                showCallOptnBtn: true,
                disableResendOtp: false,
              })
            } else {
              this.setState({
                sendOTPCounter: this.state.sendOTPCounter - 1
              })
            }
          }, 1000)
          if (this.state.setFocusOTP && !viaResendOptn) {
            document.getElementById('otp-input').focus()
          }
        })
      }
    }).catch(err => {
      console.log(err)
      this.setState({
        BtnLoaderStatus: false,
        disableResendOtp: false
      })
    })
  }

  validatePhno = () => {
    if (this.state.termsAndCondCheck) {
      if (regexCheck('validation', 'numeric', this.state.Phno, this.state.phnoLengthAr)) {
        this.setState({
          BtnLoaderStatus: true
        }, () => {
          this.fetchOTPfunc({ MobileNo : this.state.Phno }, false)
        })
      } else {
        this.props.setToaster('Please enter a valid mobile no!!', 'error')
        this.setState({
          Phno : ''
        })
      }
    }
  }

  handlePhNoInputChange = (e) => {
    if (e.which === 13) {
      e.target.value && this.state.phnoLengthAr.includes(e.target.value.length) && this.validatePhno()
    } else {
      if (regexCheck('onChange', 'numeric', e.target.value, Math.max(...this.state.phnoLengthAr))) {
        this.setState({
          Phno : e.target.value,
        })
      }
    }
  }

  // OTP funcs
  checkOTPlength = (e) => {
    if (e.which === 13) {
      e.target.value && e.target.value.length === 6 && this.checkOTP()
    } else {
      if (regexCheck('onChange', 'numeric', e.target.value, 6)) {
        this.setState({
          otpValue : e.target.value
        })
      }
    }
  }

  checkOTP = () => {
    if (!this.state.BtnLoaderStatus && this.state.otpValue) {
      this.setState({
        BtnLoaderStatus : true
      }, () => {
        let reqBody = {}
        this.state.recaptcha
        ? reqBody = {
          mobileno: this.state.Phno,
          otp: this.state.otpValue,
          recaptcha : this.state.recaptcha
        } : reqBody = {
          mobileno: this.state.Phno,
          otp: this.state.otpValue,
        }
        this.verifyOTP(reqBody)
      })
    }
  }

  verifyOTP = (reqBody) => {
    this.props.verifyOTPAsync(reqBody).then((data) => {
      console.log(data)
      if (data.sendCaptcha) {
        // window.grecaptcha
        //   ? window.grecaptcha.reset()
        // : ''
        window.grecaptcha && window.grecaptcha.reset()
        this.setState({
          loginPageId : 'otp_page',
          showReCaptcha: true,
          otpValue: '',
          BtnLoaderStatus: false
        })
        this.props.setToaster('Invalid OTP', 'error')
      } else if (data.isNew || !data.profileComplete) {
        this.setState({
          loginPageId: 'new_usr'
        })
      } else {
        this.props.getUserDetailsAsync({
          ConsumerID: this.props.user.consumerDetails.data.ConsumerID,
          AuthID : this.props.user.consumerDetails.data.accessToken.id
        }).then(() => {
          this.setState({
            BtnLoaderStatus: false
          })
          this.redirectionLink()
        }).catch((err) => {
          console.log(err)
          browserHistory.replace('/')
        })
      }
    }).catch((err) => {
      console.log(err.data)
      this.setState({
        otpValue: '',
        BtnLoaderStatus: false
      })
    })
  }

  ResendOtp = () => {
    console.log('resend OTP')
    this.setState({
      otpValue: '',
      ResendOTPRequest: true
    }, () => {
      if (this.state.frequentCalls === 60) {
        this.fetchOTPfunc({ MobileNo: this.state.Phno })
      }
    })
  }

  handleOTPViaCall = () => {
    this.setState({
      otpValue: '',
      ResendOTPRequest: true

    }, () => {
      if (this.state.otpViaCallTimer === 60) {
        this.fetchOTPfunc({ MobileNo: this.state.Phno, voiceOtp: 1 })
      }
    })
  }

  validateContactDetails = () => {
    let callApiStatus = regexCheck('validation', 'userName', this.state.UsrName)
    if (_.get(this, 'state.UsrEmailid', '').length) {
      callApiStatus = regexCheck('validation', 'emailId', this.state.UsrEmailid) && callApiStatus
    }
    if (callApiStatus) {
      // let date = new Date()
      this.props.setglobalLoaderStatus(true)
      this.props.updateUserDetailsAsync({
        AuthID : this.props.user.consumerDetails.data.accessToken.id,
        data : {
          ...this.props.user.consumerDetails.data,
          updateObj: {
            EmailID : this.state.UsrEmailid,
            Name : this.state.UsrName
          },
          CurrentDateTime : new Date().toISOString(),
          CouponCode : ''
        }
      }).then(() => {
        this.props.setglobalLoaderStatus(false)
        this.props.loadAppConfig().then(() => {
          this.redirectionLink()
        })
      }).catch(err => {
        console.log('err: ', err)
        this.props.setglobalLoaderStatus(false)
      })
    }
  }

  redirectionLink = () => {
    if (Object.keys(_.get(this, 'props.location.lastpathName', {})).length) {
      browserHistory.push({
        pathname: this.props.location.lastpathName.path,
        query: this.props.location.lastpathName.params
      }, () => {
        this.props.storeLastKnowPathName({})
      })
    } else {
      browserHistory.push('/')
    }
  }

  handleUserInputs = (e) => {
    if (e.which === 13) {
      this.validateContactDetails()
    } else {
      if (e.target.type === 'text') {
        if (regexCheck('onChange', 'userName', e.target.value, 40)) {
          this.setState({
            UsrName : e.target.value
          })
        }
      } else {
        if (regexCheck('onChange', 'emailId', e.target.value, 40)) {
          this.setState({
            UsrEmailid : e.target.value
          })
        }
      }
    }
  }

  updateOTPImage = (key) => {
    this.setState({
      otpImage : (key === 'active') ? activeOTPImg : defOTPImg
    })
  }

  handleTermsAndCondClick = () => {
    this.setState({
      termsAndCondCheck: !this.state.termsAndCondCheck
    })
  }

  recaptchaOnChange = (val) => {
    console.log('val', val)
    this.setState({
      recaptcha : val,
      recaptchaCheck: true,
    })
  }

  render () {
    let renderDiv
    if (this.state.loginPageId === 'new_usr') {
      renderDiv = <LoginContactDetails handleUserInputs={this.handleUserInputs} UsrEmailid={this.state.UsrEmailid}
        validateContactDetails={this.validateContactDetails} UsrName={this.state.UsrName}
        BtnLoaderStatus={this.state.BtnLoaderStatus} {...this.props} />
    } else if (this.state.loginPageId === 'otp_page') {
      renderDiv = <LoginOTP checkOTPlength={this.checkOTPlength} checkOTP={this.checkOTP}
        sendOTPCounter={this.state.sendOTPCounter} otpValue={this.state.otpValue} {...this.props}
        frequentCalls={this.state.frequentCalls} showOTPTimer={this.state.showOTPTimer}
        ResendOtp={this.ResendOtp} showResendOTPbtn={this.state.showResendOTPbtn}
        showCallOptnBtn={this.state.showCallOptnBtn} handleOTPViaCall={this.handleOTPViaCall}
        otpViaCallTimer={this.state.otpViaCallTimer} showCallTimer={this.state.showCallTimer}
        otpImage={this.state.otpImage} updateOTPImage={this.updateOTPImage}
        BtnLoaderStatus={this.state.BtnLoaderStatus} Phno={this.state.Phno} id={'otp-input'} user={this.props.user}
        recaptchaOnChange={this.recaptchaOnChange} recaptchaCheck={this.state.recaptchaCheck}
        showReCaptcha={this.state.showReCaptcha} allowResend={this.state.allowResend} />
    } else {
      renderDiv = <LoginPhNo handlePhNoInputChange={this.handlePhNoInputChange}
        validatePhno={this.validatePhno} Phno={this.state.Phno} phnoLengthAr={this.state.phnoLengthAr}
        selectedCountryObj={this.state.selectedCountryObj} handleCountryChange={this.handleCountryChange}
        termsAndCondCheck={this.state.termsAndCondCheck} handleTermsAndCondClick={this.handleTermsAndCondClick}
        {...this.props} BtnLoaderStatus={this.state.BtnLoaderStatus} />
    }
    let legalTxt = _.get(this, 'props.user.appConfigDetails.footerLinks.footerText')
    let poweredByText = _.get(this, 'props.user.appConfigDetails.footerLinks.poweredByText', 'Powered by Servify')
    return (
      <div className='row login-index'>
        {this.state.configFetched
          ? <div className='col-sm-12 login-container'>
            <div className='header-container mb20'>
              <img src={MainLogo} />
            </div>
            {renderDiv}
            <div className='mt20 ml-10'>
              {legalTxt
                ? <span className='Note_Text bookFontStyle fontSmall'
                  dangerouslySetInnerHTML={{ __html: legalTxt }} />
                : ''
              }
              <div className='mt10'>
                <span className='fontMedium Note_Text'>
                  {BrandSpecificConfig().showPServifyTxt && poweredByText}</span>
              </div>
            </div>
          </div>
        : ''}
      </div>
    )
  }
}

export default Login

Login.propTypes = {
  generateOTPAsync: PropTypes.func.isRequired,
  verifyOTPAsync: PropTypes.func.isRequired,
  updateUserDetailsAsync: PropTypes.func.isRequired,
  setToaster: PropTypes.func.isRequired,
  getUserDetailsAsync: PropTypes.func.isRequired,
  user: PropTypes.object.isRequired,
  login: PropTypes.object.isRequired,
  location: PropTypes.object.isRequired,
  loadAppConfig: PropTypes.func.isRequired,
  storeLastKnowPathName: PropTypes.func.isRequired,
  setSelectedCountryData: PropTypes.func.isRequired,
  getAppConfig: PropTypes.func,
  setglobalLoaderStatus: PropTypes.func.isRequired
}



