import { connect } from 'react-redux'
import Login from '../components/Login'
//
import { generateOTPAsync, verifyOTPAsync } from '../modules/login'
import { updateUserDetailsAsync, getUserDetailsAsync,
  loadAppConfig, setSelectedCountryData, getCountriesAsync } from '../../../store/user'
import { storeLastKnowPathName } from '../../../store/location'
import { setToaster, setglobalLoaderStatus } from '../../../store/toastsAndLoaders'
/*  Object of action creators (can also be function that returns object).
    Keys will be passed as props to presentational components. Here we are
    implementing our wrapper around increment; the component doesn't care   */
const mapDispatchToProps = {
  generateOTPAsync,
  verifyOTPAsync,
  updateUserDetailsAsync,
  setToaster,
  getUserDetailsAsync,
  loadAppConfig,
  storeLastKnowPathName,
  getCountriesAsync,
  setglobalLoaderStatus,
  setSelectedCountryData: (obj) => setSelectedCountryData(obj)
}

const mapStateToProps = (state) => ({
  login: state.login,
  user: state.user,
  location: state.location
})

export default connect(mapStateToProps, mapDispatchToProps)(Login)



